import React, { Component } from 'react';
import algoliasearch from 'algoliasearch';
import axios from 'axios';
import logo from './logo.svg';
import './App.css';

class App extends Component {
  state = {
    objects: []
  };
  componentDidMount() {
    axios.get('https://steamspy.com/api.php?request=all').then(res => {
      console.dir(res.data);
      this.setState(() => ({
        objects: this.handleData(res.data)
      }));
    });
  }

  handleData = data => {
    let objects = [];
    for (let i of Object.entries(data)) {
      i[1].objectID = i[0];
      try {
        i[1].tags = Object.keys(i[1].tags)
      } catch(err){
        console.log(err);
      }
      objects.push(i[1]);
    }
    return objects;
  };

  render() {
    const client = algoliasearch(
      'VICM0QATAN',
      '36fff9ff4adff96002e8219dbdecff1e'
    );
    const index = client.initIndex('steamgame');
    const objects = this.state.objects.slice()

    index.addObjects(objects, (err, content) => {
      console.log(content);
    })
    return (
      <div className="App">
       hello
      </div>
    )
  }
}

export default App;



